import React, { useState } from 'react';
import TextInput from '../Input';
import Chart from '../Chart';
import Analysis from './Analysis';
import { Grid } from '@material-ui/core';
import lineIntersect from '@turf/line-intersect';
import lineString from 'turf-linestring';
import { makeStyles } from '@material-ui/styles';


const useStyles = makeStyles(theme => ({
    container: {
        padding: theme.spacing(2)
    }
}));


const Day3 = () => {

    const [data, setData] = useState([]);

    const [data3D, set3DData] = useState([]);

    const [intersects, setIntersects] = useState([]);

    const [intersectSteps, setIntersectSteps] = useState([]);

    const [isLoading, setLoading] = useState(false);

    const classes = useStyles();

    const setInput = (inputData, input3D) => {
        setLoading(true);
        const calculateData = () => {
            new Promise(resolve => {
                const lines = inputData.map(d => lineString(d.x.map((x, i) => [x, d.y[i]])));
                if (lines.length !== 0) {
                    const result = lineIntersect(...lines).features;
                    setIntersects(result
                        .sort((a, b) => Math.abs(a.geometry.coordinates[0]) + Math.abs(a.geometry.coordinates[1]) - Math.abs((b.geometry.coordinates[0]) - Math.abs(b.geometry.coordinates[1]))))

                    let stepsIntersec = result.map(intersect => {
                        const line1Count = inputData[0].x.findIndex((x, i) => x === intersect.geometry.coordinates[0] && inputData[0].y[i] === intersect.geometry.coordinates[1]) + 1;
                        const line2Count = inputData[1].x.findIndex((x, i) => x === intersect.geometry.coordinates[0] && inputData[1].y[i] === intersect.geometry.coordinates[1]) + 1;
                        return {
                            x: intersect.geometry.coordinates[0],
                            y: intersect.geometry.coordinates[1],
                            line1Count,
                            line2Count
                        }
                    }).sort((a, b) => Math.abs(a.x) + Math.abs(a.y) - Math.abs(b.x) - Math.abs(b.y));
                    const dataWithIntersects = [...inputData];
                    let maxSteps = stepsIntersec.reduce((count, i) => count < (Math.abs(i.x) + Math.abs(i.y)) ? (Math.abs(i.x) + Math.abs(i.y)) : count, 0);
                    const intersectPlot = {
                        type: 'scatter',
                        mode: 'markers',
                        marker: {
                            color: stepsIntersec.map((i, index) => index === 0 ? 'black' : 40 - ((Math.abs(i.x) + Math.abs(i.y)) / maxSteps * 40))
                        },
                        name: `Intersections`,
                        x: stepsIntersec.map(i => i.x),
                        y: stepsIntersec.map(i => i.y),
                        text: stepsIntersec.map((i, index) => `${index === 0 ? 'Best Value Solution 1: ' : 'Distance to 0/0:'} ${i.x + i.y}`),
                    }
                    dataWithIntersects.push(intersectPlot)
                    setData(dataWithIntersects);
                    stepsIntersec = [...stepsIntersec].sort((a, b) => a.line1Count + a.line2Count - b.line1Count - b.line2Count);
                    setIntersectSteps(stepsIntersec);
                    const data3DIntersetction = [...input3D];
                    maxSteps = stepsIntersec.reduce((count, i) => count < (i.line1Count + i.line2Count) ? i.line1Count + i.line2Count : count, 0);
                    stepsIntersec.forEach((intersect, index) => {
                        const z1 = input3D[0].x.findIndex((x, i) => x === intersect.x && input3D[0].y[i] === intersect.y) + 1;
                        const z2 = input3D[1].x.findIndex((x, i) => x === intersect.x && input3D[1].y[i] === intersect.y) + 1;
                        data3DIntersetction.push({
                            type: 'scatter3d',
                            mode: 'lines',
                            line: {
                                color: index === 0 ? '#000' : perc2color((maxSteps - intersect.line1Count - intersect.line2Count) / maxSteps * 100)
                            },
                            name: `${index === 0 ? 'Best Value Solution 2: ' : ''}${intersect.x}/${intersect.x}/${z1 + z1}`,
                            x: [intersect.x, intersect.x],
                            y: [intersect.y, intersect.y],
                            z: [z1, z2]
                        });
                    });
                    set3DData(data3DIntersetction)
                }
                setLoading(false);
                resolve();
            });
        }
        setTimeout(calculateData);
    };


    return <Grid container={true} spacing={1} className={classes.container}>
        <Grid item={true} xs={12}>
            <TextInput
                day={3}
                isLoading={isLoading}
                setInput={setInput} />
        </Grid>
        <Grid xs={12} md={6} item={true}>
            <Chart data={data} />
        </Grid>
        <Grid xs={12} md={6}item={true}>
            <Chart data={data3D} />
        </Grid>
        <Grid xs={12} md={12} item={true}>
            <Analysis
                intersects={intersects}
                intersectSteps={intersectSteps} />
        </Grid>
    </Grid>
}


function perc2color(perc) {
    let r, g, b = 0;
    if (perc < 50) {
        r = 255;
        g = Math.round(5.1 * perc);
    }
    else {
        g = 255;
        r = Math.round(510 - 5.10 * perc);
    }
    const h = r * 0x10000 + g * 0x100 + b * 0x1;
    return '#' + ('000000' + h.toString(16)).slice(-6);
}


export default Day3;