import React, { useState } from 'react';
import { CssBaseline, AppBar, Toolbar, Typography, IconButton, Tabs, makeStyles, Tab } from '@material-ui/core';
import TabPanel from './TabPanel';
import Day3 from './day3';
import Day8 from './day8';



const useStyles = makeStyles({
    title: {
        flexGrow: 1,
    }
});


const Content = () => {

    const classes = useStyles();

    const [selectedDay, setDay] = useState(0);

    return <>
        <CssBaseline />
        <AppBar position="static">
            <Toolbar>
                <Typography variant="h6" className={classes.title}>
                    Advent of Code
                </Typography>
                <Tabs value={selectedDay} onChange={(e, v) => setDay(v)}>
                    <Tab label="Day 3" value={0} />
                    <Tab label="Day 8" value={1} />
                </Tabs>
                <IconButton onClick={() => window.location.href = 'https://github.com/Domino987/aoc_3_19'}>
                    <img height={25} alt='Repo' src='/assets/github.png' />
                </IconButton>
            </Toolbar>
        </AppBar>
        <TabPanel value={selectedDay} index={0}>
            <Day3 />
        </TabPanel>
        <TabPanel value={selectedDay} index={1}>
            <Day8 />
        </TabPanel>
    </>
}



export default Content;