import React from 'react';
import { Grid, Table, TableHead, TableRow, TableCell, TableBody, CardHeader, CardContent, Card } from '@material-ui/core';

const Analysis = ({ intersects, intersectSteps }) => {

    return <div>
        <Grid container={true}>
            <Grid item={true} xs={12} lg={6}>
                <Card>
                    <CardHeader title={`Intersections Solution 1 (${intersects.length !== 0 ? Math.abs(intersects[0].geometry.coordinates[0]) + Math.abs(intersects[0].geometry.coordinates[1]) : 0})`} />
                    <CardContent>
                        <Table size="small" aria-label="a dense table">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Number</TableCell>
                                    <TableCell>Location (x/y)</TableCell>
                                    <TableCell>Distance to Center</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {intersects.map((row, i) => (
                                    <TableRow key={i}>
                                        <TableCell component="th" scope="row">
                                            {i}
                                        </TableCell>
                                        <TableCell>{row.geometry.coordinates[0]}/{row.geometry.coordinates[1]}</TableCell>
                                        <TableCell>{Math.abs(row.geometry.coordinates[0]) + Math.abs(row.geometry.coordinates[1])}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>
            </Grid>
            <Grid item={true} xs={12} lg={6}>
                <Card>
                    <CardHeader title={`Intersections Solution 2 (${intersectSteps.length !== 0 ? (intersectSteps[0].line1Count + intersectSteps[0].line2Count) : 0})`} />
                    <CardContent>
                        <Table size="small" aria-label="a dense table">
                            <TableHead>
                                <TableRow>
                                    <TableCell>Number</TableCell>
                                    <TableCell align="right">Location (x/y)</TableCell>
                                    <TableCell align="right">Steps Wire 1</TableCell>
                                    <TableCell align="right">Steps Wire 2</TableCell>
                                    <TableCell align="right">Total Steps</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {intersectSteps.map((row, i) => (
                                    <TableRow key={i}>
                                        <TableCell component="th" scope="row">
                                            {i}
                                        </TableCell>
                                        <TableCell align="right">{row.x}/{row.y}</TableCell>
                                        <TableCell align="right">{row.line1Count}</TableCell>
                                        <TableCell align="right">{row.line2Count}</TableCell>
                                        <TableCell align="right">{row.line1Count + row.line2Count}</TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>
            </Grid>

        </Grid>

    </div>
}


export default Analysis;