import React, { useState } from 'react';
import TextInput from '../Input';
import { Grid, Theme, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import Chart from '../Chart';


const useStyles = makeStyles((theme: Theme) => ({
    container: {
        padding: theme.spacing(2)
    }
}));

const imgHeight = 6
const imgWidth = 25

const calcImgData = (data: number[]) => {
    const imgSize = imgHeight * imgWidth;
    const layers = seperateLayers(data, imgSize)
    let fewest0 = undefined
    let minCount = Math.pow(2, 53) - 1;
    layers.forEach(layer => {
        const count = countDigits(layer, 0)
        if (count < minCount) {
            minCount = count
            fewest0 = layer
        }
    });
    if (!fewest0) {
        return -1;
    }
    return countDigits(fewest0, 1) * countDigits(fewest0, 2)
}


const seperateLayers = (data: number[], n: number) => {
    const result: number[][] = [];
    for (let i = 0; i < data.length; i += n) {
        result.push(data.slice(i, i + n))
    }
    return result;
}


const calculateImages = (data: number[]) => {
    const imgSize = imgHeight * imgWidth;
    const layers = seperateLayers(data, imgSize)
    layers.reverse()
    let img = Array.from(Array(imgSize), () => 0);
    layers.forEach(layer => {
        img = mergeLayer(img, layer)
    });
    const imgRows = seperateLayers(img, imgWidth);
    return transformToPlotly(imgRows);
}


const mergeLayer = (img: number[], layer: number[]) => {
    const imgSize = imgHeight * imgWidth;
    const copy = [...layer]
    for (let i = 0; i < imgSize; i++) {
        if (copy[i] === 2) {
            copy[i] = img[i]
        }
    }
    return copy
}


const countDigits = (layer: number[], number: number) => {
    let count = 0
    layer.forEach(digit => {
        if (digit === number) {
            count += 1;
        }
    });
    return count
}

const transformToPlotly = (data: number[][]) => {
    const xData: number[] = [0]
    const yData: number[] = [imgHeight]
    data.reverse();
    for (let y = 0; y < imgHeight; y++) {
        const row = data[y];
        for (let x = 0; x < imgWidth; x++) {
            if (row[x] === 1) {
                xData.push(x)
                yData.push(y)
            }
        }
    }
    return [{
        x: xData,
        y: yData,
        mode: 'markers',
        marker: {
            color: ['#FFF', ...xData.slice(1).map(() => '#000')]
        }
    }];
}



const View = () => {

    const [bestLayer, setBestLayer] = useState(-1);

    const [imgData, setImgData] = useState<Array<{ x: number[], y: number[], mode: string }>>([]);

    const [isLoading, setLoading] = useState(false);

    const classes = useStyles();

    const setInput = async (input: string) => {
        setLoading(true)
        await new Promise(resolve => {
            const data = input.split('').map(Number);
            setBestLayer(calcImgData(data));
            setImgData(calculateImages(data));
        });
        setLoading(false);

    };


    return <Grid container={true} spacing={1} className={classes.container}>
        <Grid item={true} xs={12}>
            <TextInput
                day={8}
                isLoading={isLoading}
                setInput={setInput} />
        </Grid>
        {bestLayer !== -1 && <Grid item={true} xs={12}>
            <Typography>
                Solution 1
            </Typography>
            <Typography>
                The value of the layer with the last amount of zeros is: {bestLayer}
            </Typography>
        </Grid>}
        {bestLayer !== -1 && <Grid item={true} xs={12}>
            <Typography>
                Solution 2
            </Typography>
            <Grid xs={12} item={true}>
                <Chart data={imgData} />
            </Grid>
        </Grid>}

    </Grid>
}



export default View;