import React from 'react';
import Content from './components/Content';
import { createMuiTheme, ThemeProvider } from '@material-ui/core';

const theme = createMuiTheme({});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <div className="App">
        <main>
          <Content />
        </main>
      </div>
    </ThemeProvider>

  );
}

export default App;
